.card-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 100vh;
    background-color: #1e1e1e;
    color: white;
    position: relative;
}

/* Bouton de sortie */
.exit-button {
    position: absolute;
    top: 20px;
    left: 20px;
    background: none;
    border: none;
    font-size: 30px;
    color: white;
    cursor: pointer;
}

/* Compteur de progression */
.progress-counter {
    position: absolute;
    top: 20px;
    font-size: 24px;
}

/* Scores */
.score-container {
    position: absolute;
    top: 60px;
    width: 100%;
    display: flex;
    justify-content: space-between;
    padding: 0 40px;
}

.score-circle {
    width: 50px;
    height: 50px;
    margin-top: 15px;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 50%;
    font-size: 24px;
    font-weight: bold;
    color: white;
}

.incorrect-score {
    background-color: red;
}

.correct-score {
    background-color: limegreen;
}

/* Flip Card */
.flip-card {
    perspective: 1000px;
    width: 100%;
    max-width: 500px;
    height: 250px;
}

.flip-card-inner {
    width: 100%;
    height: 100%;
    position: relative;
    transform-style: preserve-3d;
    transition: transform 0.6s;
}

.flip-card-inner.flipped {
    transform: rotateY(180deg);
}

.flip-card-front,
.flip-card-back {
    position: absolute;
    width: 100%;
    height: 100%;
    backface-visibility: hidden;
    top: 0;
    left: 0;
}

.flip-card-front {
    z-index: 2;
    transform: rotateY(0deg);
    background-color: #ffffff;
}

.flip-card-back {
    transform: rotateY(180deg);
    z-index: 1;
    background-color: #dff0d8;
}


/* Boutons de réponse */
.button2-group {
    display: flex;
    flex-direction: row;
    gap: 50px;
    margin-top: 20px;
}

.incorrect-button,
.correct-button {
    font-size: 24px;
    padding: 10px;
    border: none;
    border-radius: 50%;
    width: 50px;
    height: 50px;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
}

.incorrect-button {
    background-color: red;
    color: white;
}

.correct-button {
    background-color: limegreen;
    color: white;
}
